import React from "react";
import Link from "next/link";

const Nav = () => {
  return (
    <nav className="nav">
      <div className="hamburger">
        <span />
        <span />
        <span />
      </div>

      <ul className="nav__ul">
        <li>
          <Link href="/home" style={linksStyle}>
            {" "}
            Home
          </Link>
        </li>
        <li>
          <Link href="/about" style={linksStyle}>
            {" "}
            About Me
          </Link>
        </li>
        <li>
          <Link href="/portfolio" style={linksStyle}>
            {" "}
            Portfolio
          </Link>
        </li>
        <li>
          <Link href="/contact" style={linksStyle}>
            {" "}
            Contact
          </Link>
        </li>
      </ul>
    </nav>
  );
};

const linksStyle = { textDecoration: "none" };
export default Nav;
